import React, { useState } from 'react';
import { BsFillFileEarmarkFill } from 'react-icons/bs';
import { MdOutlineMoreVert } from 'react-icons/md';
import styled from 'styled-components';
import { AiFillEye } from 'react-icons/ai';
import { RiDeleteBinLine } from 'react-icons/ri';
import { Link } from 'react-router-dom';
import './index.css';

const Icon = styled(BsFillFileEarmarkFill)`
    height: 100%;
`;

export default function DocumentCard({document}) {
    const [openMoreOptions, setOpenMoreOptions] = useState(false);
    return (
        <div className="document_card">
            <div className="more_option_box" onClick={() => setOpenMoreOptions(!openMoreOptions)}>
                <MdOutlineMoreVert color="#5E5E5E" size={17} />
            </div>
            <Icon size={100} color="#E2EEF6" />
            <div className="document_card_content">
                <div className="document_card_title">{document.uniqueId}</div>
                <p>{document.document}</p>
            </div>
            {openMoreOptions && (
                <div className="more_option_content_box">
                    <Link to={`/get-prediction-results/${document.uniqueId}`} className="option_box">
                        <AiFillEye color="#FF6600" size={17} />
                    </Link>
                    <div className="option_box">
                        <RiDeleteBinLine color="#FF6600" size={17} />
                    </div>
                </div>
            )}
        </div>
    );
}
