import React, { useState, useEffect } from 'react';
import DocumentCard from './components/DocumentCard';
import SojiBaseInit from '@modules/SojiBase';
import PagePreloader from '@components/preloader/PagePreloader';

export default function RecentDocument() {
    const [state, setState] = useState({
        fetchedData: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('v1/suspected_users/get-all_recent_documents');
            getter.on('static', async (response) => {
                const data = response.response.data;
                stateHandler('fetchedData', data);
            });
        })();
    }, []);

    return (
        <div className="container content_Container">
            {state.fetchedData ? (
                <div className="row">
                    {state.fetchedData.map((document, index) => {
                        return (
                            <div className="col-md-4" key={index}>
                                <DocumentCard document={document} />
                            </div>
                        );
                    })}
                </div>
            ) : (
                <PagePreloader />
            )}
        </div>
    );
}
